import styled from "styled-components"
import { Styles } from "../../constants/styles"

const Styled = styled.div`
    &.wrapper{
        ${Styles.classes.flex};
        &.justifyCenter{
            ${Styles.classes.justifyCenter};
        }
        &.alignStart{
            ${Styles.classes.alignStart}
        }
    }
`

export default function Wrapper({children , ...rest}) {
  return (
    <Styled className="wrapper" {...rest}>{children}</Styled>
  )
}
