import Skeleton from "react-loading-skeleton";
import { useSelector } from "react-redux";
import styled from "styled-components"
import { Styles } from "../../constants/styles";
import Theme from "../skeleton-theme/Theme";

const Styled = styled.button`
  padding:${Styles?.sizes?.xs} ${Styles?.sizes?.lg};
  border-radius:0.5rem;
  border:1px solid ${props => props.transparent ? 'white' : 'transparent'};
  background-color:${props => props.transparent ? 'transparent' : "#D6FD3A"};
  color:${props => props.transparent ? 'white' : "black"};
  font-weight:bold;
  position:relative;
  z-index:0;
  overflow:hidden;
  transition: all 0.5s;

  &::after{
    content:"";
    width:0%;
    height:0%;
    background-color:${props => props.transparent ? "#D6FD3A" : Styles?.colors?.white};
    position:absolute;
    top:-200%;
    left:-200%;
    z-index:-1;
    transform:rotate(5deg);
    transition: all 0.5s;
    border-radius:1rem;
  }

  &:hover{
    color:${props => props.transparent ? "black" : Styles?.colors?.lightBlue};
    animation: wiggle 2s linear 1;
    &::after{
      width:300%;
      height:300%;
      top:-50%;
      left:-50%;
    }
  }
  @keyframes wiggle {
    0%, 7% {
      transform: rotateZ(0);
    }
    15% {
      transform: rotateZ(-15deg);
    }
    20% {
      transform: rotateZ(10deg);
    }
    25% {
      transform: rotateZ(-10deg);
    }
    30% {
      transform: rotateZ(6deg);
    }
    35% {
      transform: rotateZ(-4deg);
    }
    40%, 100% {
      transform: rotateZ(0);
    }
  }
`;

export default function Button({ children, ...rest }) {
  const { loading } = useSelector(state => state.content);
  return (
    <Styled {...rest}>
      <Theme color="white">
        {loading ? <Skeleton width="70px"/> : children}
      </Theme>
    </Styled>
  )
}
