import styled from "styled-components";
import logo from '../../assets/images/logo.png'
import { Wrapper, Button } from "../../components";
import { Styles } from "../../constants/styles";
import { useDispatch } from "react-redux";
import { toggleOffcanvas } from '../../redux/slices/content'
const Styled = styled.div`
    &.nav{
        padding:1rem 0;
        .nav{
            &__menu{
                ul{
                    display:flex;
                    gap:2rem;
                    a{
                        color:${Styles?.colors?.grey};
                        &:hover{
                            color:${Styles?.colors?.white};
                        }
                    }
                }
            } // menu
            &__right{
                display:flex;
                align-items:center;
                gap:1rem;
            }
            @media screen and (max-width:480px){
                &__logo{
                    width:120px;
                }
            }
        }
    }
`;

export default function Navbar({ ...rest }) {
    const dispatch = useDispatch();


    return (
        <Styled className="nav" {...rest}>
            <div className="container">
                <Wrapper>
                    <div className="nav__logo">
                        <img src={logo} alt="logo" loading="lazy" />
                    </div>
                    <div className="nav__right">
                        <div className="nav__btn">
                            <a href="#contact">
                                <Button>
                                    Get Started
                                </Button>
                            </a>
                        </div>
                        <div className="nav__bars" onClick={() => dispatch(toggleOffcanvas())}>
                            <svg width="22" height="13" viewBox="0 0 22 13" fill="none" xmlns="http://www.w3.org/2000/svg">
                                <path d="M1.1 2.16667H20.9C21.1917 2.16667 21.4715 2.05253 21.6778 1.84937C21.8841 1.6462 22 1.37065 22 1.08333C22 0.796016 21.8841 0.520466 21.6778 0.317301C21.4715 0.114137 21.1917 0 20.9 0H1.1C0.808262 0 0.528473 0.114137 0.322183 0.317301C0.115892 0.520466 0 0.796016 0 1.08333C0 1.37065 0.115892 1.6462 0.322183 1.84937C0.528473 2.05253 0.808262 2.16667 1.1 2.16667ZM20.9 10.8333H1.1C0.808262 10.8333 0.528473 10.9475 0.322183 11.1506C0.115892 11.3538 0 11.6293 0 11.9167C0 12.204 0.115892 12.4795 0.322183 12.6827C0.528473 12.8859 0.808262 13 1.1 13H20.9C21.1917 13 21.4715 12.8859 21.6778 12.6827C21.8841 12.4795 22 12.204 22 11.9167C22 11.6293 21.8841 11.3538 21.6778 11.1506C21.4715 10.9475 21.1917 10.8333 20.9 10.8333ZM20.9 5.41667H1.1C0.808262 5.41667 0.528473 5.5308 0.322183 5.73397C0.115892 5.93713 0 6.21268 0 6.5C0 6.78732 0.115892 7.06287 0.322183 7.26603C0.528473 7.4692 0.808262 7.58333 1.1 7.58333H20.9C21.1917 7.58333 21.4715 7.4692 21.6778 7.26603C21.8841 7.06287 22 6.78732 22 6.5C22 6.21268 21.8841 5.93713 21.6778 5.73397C21.4715 5.5308 21.1917 5.41667 20.9 5.41667Z" fill="white" />
                            </svg>
                        </div>
                    </div>
                </Wrapper>
            </div>
        </Styled>
    )
}
